let initialState = {
    messages: [
        { id: 1, message: 'first message'},
        { id: 2, message: 'second message'},
        { id: 3, messgae: 'third message'},
        { id: 4, message: 'fourth message'},
        { id: 5, message: 'fiveth message'},
        { id: 6, message: 'sixth  message'}
    ],
    dialogs: [
        { id: 1, name: 'Nurzhan' },
        { id: 2, name: 'Nurken' },
        { id: 3, name: 'Dauren' },
        { id: 4, name: 'Tamirkhan' },
        { id: 5, name: 'Birzhan' },
        { id: 6, name: 'Kusain' }
    ],
    newMessage: 'message',
}

const dialogsPageReducer = (state = initialState , action) => {

    let copyState;

    switch(action.type){
        case 'ADD-MESSAGE':
            let message = action.message; 
            return {
                ...state,
                newMessage: '',
                messages: [...state.messages, { id: 7, message}]
            }
        default:
            return state;
    }
}

export default dialogsPageReducer;

export let messageAddActionCreator = (message) => {
    return {
        type: "ADD-MESSAGE",
        message
    }
}