import { connect } from "react-redux"
import { Field, reduxForm } from "redux-form"
import { required } from "../../utils/validators"
import { renderField } from "../Textarea/FormControl"
import { loginThunk } from "../../redux/authReducer"
import { Redirect } from "react-router-dom"
import styles from "./../Textarea/FormControl.module.css"

const LoginForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field name={"email"} component={renderField} placeholder={"email"} validate={[required]} type="email" />
            </div>
            <div>
                <Field name={"password"} component={renderField} placeholder={"password"} validate={[required]} type="password" />
            </div>
            <div>
                <Field name={"rememberMe"} type={"checkbox"} component={renderField} /> Remember Me?
            </div>
            <div className={styles.formSummaryError}>
                {props.error}
            </div>
            <div>
                <button>Log In</button>
            </div>
        </form>
    )
}

const LoginReduxForm = reduxForm({
    form: 'login'
})(LoginForm)

const Login = (props) => {
    console.log(props)
    let onSubmit = (formData) => {
        props.loginThunk(formData.email, formData.password, formData.rememberMe)
    }

    if(props.isAuth) {
        return <Redirect to={"/profile"} />
    }

    return (
        <div>
            <h1>Login</h1>
            <LoginReduxForm onSubmit={onSubmit} />
        </div>
    )
}

let mapStateToProps = (state) => {
    return {
        isAuth: state.auth.isAuth
    }
}
// export default Login;
export default connect(mapStateToProps, { loginThunk })(Login)