import { NavLink } from 'react-router-dom';
import { logoutThunk } from '../../redux/authReducer';
import s from './Header.module.css';

const Header = (props) => {
    return (
        <header className={s.header}>
            <img src="https://logos-world.net/wp-content/uploads/2020/04/Huawei-Logo.png" />
            {props.data.isAuth ? <button onClick={props.logoutThunk}> {props.data.login} - logout</button> : <NavLink to={"/login"}>login</NavLink>}
            {/* {props.data.id === null ? <NavLink to='/login'>login</NavLink> : <NavLink to={props.data.login}>{props.data.login}</NavLink>} */}
        </header>
    );
}

export default Header;