import {getUsers, setUnfollow, setFollow} from '../api/api'

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS =  'SET_USERS';
const SET_CURRENT_PAGE =  'SET_CURRENT_PAGE';
const SET_USERS_TOTAL_COUNT = 'SET_USERS_TOTAL_COUNT';
const IS_FETCHING = 'IS_FETCHING';
const FOLLOW_IN_PROGRESS = 'FOLLOW_IN_PROGRESS';

let initialState = {
    users: [],
    totalUsersCount: 0,
    pageSize: 10,
    currentPage: 1,
    isFetching: false,
    followInProgress: []
}

const usersPageReducer = (state = initialState, action)  => {
    switch(action.type){
        case FOLLOW:
            return {
                ...state,
                users: state.users.map(u => {
                    if(u.id === action.userId){
                        return {...u, followed: true}
                    }
                    return u;
                })
            } 

        case UNFOLLOW:
            return {
                ...state, 
                users: state.users.map( u => {
                    if(u.id === action.userId){
                        return {...u, followed: false}
                    }
                    return u;
                })
            }
        case SET_USERS:
            return {
                ...state, users: action.users
            }
        case SET_CURRENT_PAGE:
            return {
                ...state, currentPage: action.currentPage
            }
        case SET_USERS_TOTAL_COUNT:
            return {
                ...state, totalUsersCount: action.totalUsersCount
            }
        case IS_FETCHING:
            return {
                ...state, isFetching: action.isFetching
            }
        case FOLLOW_IN_PROGRESS:
            return {
                ...state,
                followInProgress: action.isFetching ?
                [...state.followInProgress, action.userId] 
                : state.followInProgress.filter(id => id != action.userId)
            }
        default:
            return state;
    }
}

export default usersPageReducer;

export const followActionCreator = (userId) => {
    return {
        type: FOLLOW,
        userId: userId
    }
}

export const unfollowActionCreator = (userId) => {
    return {
        type: UNFOLLOW,
        userId: userId
    }
}

export const currentPageActionCreator = (currentPage) => {
    return {
        type: SET_CURRENT_PAGE,
        currentPage: currentPage
    }
}

export const setUsersActionCreator = (users) => {
    return {
        type: SET_USERS,
        users: users
    }
}

export const setUsersTotalCountActionCreator = (count) => {
    return {
        type: SET_USERS_TOTAL_COUNT,
        totalUsersCount: count
    }
}

export const isFetchingActionCreator = (isFetching) => {
    return{ 
        type: IS_FETCHING,
        isFetching
    }
}

export const followInProgressActionCreator = (isFetching, userId) => {
    return{
        type: FOLLOW_IN_PROGRESS,
        isFetching: isFetching, userId
    }
}


export const usersGetThunk = (currentPage, pageSize) => {
    return (dispatch) => {
        dispatch(isFetchingActionCreator(true))
        getUsers(currentPage, pageSize) 
            .then(data => { dispatch(isFetchingActionCreator(false));
                            dispatch(setUsersActionCreator(data.items)); 
                            dispatch(setUsersTotalCountActionCreator(data.totalCount));
                        })
    }
}
    
export const currentPageThunk = (pageNumber, pageSize) => {
    return (dispatch) => {
        dispatch(currentPageActionCreator(pageNumber))
        dispatch(isFetchingActionCreator(true))
        getUsers(pageNumber, pageSize)
            .then(data => { dispatch(isFetchingActionCreator(false))
                            dispatch(setUsersActionCreator(data.items));
            })
    }
}

export const unfollowThunk  = (userId) => {
    return (dispatch) => {
        setUnfollow(userId)
        .then(response => {
            if(response.data.resultCode == 0){
                dispatch(unfollowActionCreator(userId))
            }
            dispatch(followInProgressActionCreator(false, userId))
        })
    }
}

export const followThunk = (userId) => {
    return (dispatch) => {
        setFollow(userId)
        .then(response => {
            if(response.data.resultCode == 0)
            {
                dispatch(followActionCreator(userId));
            }
            dispatch(followInProgressActionCreator(false, userId))
        })
    }
}

