import React from 'react';
import { connect } from 'react-redux';
import Profile from './Profile';
import { addPostActionCreator, updateNewPostActionCreator, setUserProfileActionCreator,
         getProfileThunk, updateStatusThunk, getStatusThunk } from './../../redux/profilePageReducer'
import { withRouter } from 'react-router';
import { withAuthRedirect } from '../../hoc/withAuthRedirect'
import { compose } from 'redux';


export class ProfileConainer extends React.Component {
    componentDidMount() {
        let userId = this.props.match.params.userId;
        if(!userId) {
            userId = this.props.id;
        }
        this.props.getProfileThunk(userId)
        // this.props.updateStatusThunkCreator()
        this.props.getStatusThunk(userId)
    }

    
    render() {
        return (
            <Profile {...this.props} status={this.props.status} profile={this.props.profile} updateStatus={this.props.updateStatusThunk}/>
        )
    }
}

let mapStateToProps = (state) => {
    return {
        profile: state.profilePage.profile,
        status: state.profilePage.status,
        id: state.auth.id,
        isAuth: state.auth.isAuth,
    }
}

export default compose(
    connect(mapStateToProps, {addPostActionCreator: addPostActionCreator,
        updateNewPostActionCreator: updateNewPostActionCreator,
        setUserProfileActionCreator: setUserProfileActionCreator,
        getProfileThunk: getProfileThunk,
        updateStatusThunk: updateStatusThunk,
        getStatusThunk: getStatusThunk,
    }),
    withRouter
)(ProfileConainer);