import { stopSubmit } from 'redux-form';
import {getAuth, login, logout} from '../api/api'

const SET_AUTH_USER = "SET_AUTH_USER"

let initialState = {
    id: null,
    email: null,
    login: null,
    isAuth: false,
}

export const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_AUTH_USER:
            return {
                ...state,
                ...action.payload
            };
        default:
            return state;
    }
}

export let setAuthUserActionCreator = (id, email, login, isAuth) => {
    return{
        type: SET_AUTH_USER,
        payload: { id, email, login, isAuth },
    }
}

export let getAuthThunk = () => async (dispatch) => {
    let response = await getAuth();
    let { id, email, login } = response.data;
    if (response.resultCode === 0) {
        dispatch(setAuthUserActionCreator(id, email, login, true))
    }
}


export let loginThunk = (email, password, rememberMe = false) => async (dispatch) => {
    let response = await login(email, password, rememberMe)
    if (response.data.resultCode === 0) {
        dispatch(setAuthUserActionCreator())
    } else {
        let message = response.data.messages ? response.data.messages[0] : "some error";
        dispatch(stopSubmit('login', { _error: message }))
    }
}


export let logoutThunk = () => async (dispatch) => {
    let response = await logout()
    if (response.data.resultCode === 0) {
        dispatch(setAuthUserActionCreator(null, null, null, false))
    }

}