import React from 'react';
import { connect } from 'react-redux';
import Header from './Header';
import { setAuthUserActionCreator, getAuthThunk, logoutThunk } from '../../redux/authReducer'
import axios from 'axios';
import {getAuth} from '../../api/api'

class HeaderContainer extends React.Component {
    componentDidMount() {
        this.props.getAuthThunk()
    }
    

    render() {
        return <Header {...this.props} logoutThunk={this.props.logoutThunk} />
    }
}

let mapStateToProps = (state) => {
    return {
        data: state.auth
    }
}


export default connect(mapStateToProps, {setAuthUserActionCreator: setAuthUserActionCreator,
                                         getAuthThunk: getAuthThunk,
                                         logoutThunk: logoutThunk
                                        })(HeaderContainer);