import {getProfile, updateStatus, getStatus} from '../api/api'

let ADD_POST = 'ADD_POST';
let UPDATE_NEW_POST = 'UPDATE_NEW_POST';
let SET_USER_PROFILE = 'SET_USER_PROFILE';
let SET_STATUS = "SET_STATUS";

let initialState = {
    posts: [
        { id: 1, text: 'post1', likesCount: 10 },
        { id: 2, text: 'post2', likesCount: 21 },
        { id: 3, text: 'post3', likesCount: 53 },
        { id: 4, text: 'post4', likesCount: 170 },
        { id: 5, text: 'post5', likesCount: 120 }
    ],
    newPost: 'text',
    profile: {
        userId: 1,
        lookingForAJob: false,
        ookingForAJobDescription: '',
        fullName: 'name',
        contacts: {
            github: 'github',
            vk: 'vk',
            facebook: 'facebook',
            instagram: 'instagram',
            twitter: 'twitter',
            website: 'website',
            youtube: 'youtube',
            mainLink: 'mainLink',
        },
        photos: {
            small: null,
            large: null
        } 
    },
    status: '',
}

const profilePageReducer = (state = initialState, action) => {
    let stateCopy;
    
    switch (action.type) {
        case ADD_POST: {
            let post = action.post;
            return { 
                ...state,
                newPost: '',
                posts: [...state.posts, {id: 6, text: post, likesCount: 0}]
            };
        }
        case UPDATE_NEW_POST: {
            return { 
                ...state,
                newPost: action.newPost
            }
        }
        case SET_USER_PROFILE: {
            return {
                ...state,
                profile: action.profile,
                contacts: action.profile.contacts,
                photo: action.profile.photo
            }
        }
        case SET_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }
        default:
            return state;
    }
}

export default profilePageReducer;

export let addPostActionCreator = (post) => {
    return {
        type: ADD_POST,
        post
    }
}

export let setStatusActionCreator = (status) => {
    return {
        type: SET_STATUS,
        status: status
    }
}

export let updateNewPostActionCreator = (text) => {
    return {
        type: UPDATE_NEW_POST,
        newPost: text
    }
}

export let setUserProfileActionCreator = (profile) => {
    return {
        type: SET_USER_PROFILE,
        profile: profile
    }
}


export const getProfileThunk = (userId) => {
    return (dispatch) => {
        getProfile(userId)
        .then(response => {
            dispatch(setUserProfileActionCreator(response.data))
        })
    }
}

export const getStatusThunk = (userId) => {
    return (dispatch) => {
        getStatus(userId).then(response => {
            dispatch(setStatusActionCreator(response.data))
        })
    }
}

export const updateStatusThunk = (status) => {
    return (dispatch) => {
        updateStatus(status)
        .then(response => {
            if( response.data.resultCode === 0)
            {
                dispatch(setStatusActionCreator(status));
            }
        });
    }
}
