
import s from './MyPosts.module.css'
import Post from './Posts/Post';
import React from 'react';
import { Field, reduxForm } from 'redux-form';
import { renderField } from '../../Textarea/FormControl';
import { maxLenghCreator } from '../../../utils/validators';


const maxLength30 = maxLenghCreator(30)

const MyPostsForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <Field component={renderField} name="post" validate={[maxLength30]} />
            <button>Add Post</button>
        </form>
    )
}

const MyPostsReduxForm = reduxForm({form: 'post'})(MyPostsForm)

const MyPosts = (props) => {

    let NewPostsData = props.profilePage.map((el) => {
        return <Post text={el.text} likes={el.likesCount} />;
    })

    let AddPost = (values) => {
        props.addPost(values.post);
    }

    return (
        <div className={s.posts_list}>
            My posts
            <div className={s.addPostForm}>
                <div>
                    <MyPostsReduxForm onSubmit={AddPost}/>
                </div>
            </div>
            <div className={s.posts}>
                {NewPostsData}
            </div>
        </div>
    );
}

export default MyPosts;