import classes from './App.module.css';
import Nav from './components/NavBar/Nav';
import { Route, withRouter } from 'react-router-dom';
import News from './components/News/News';
import Music from './components/Music/Music';
import Settings from './components/Settings/Settings';
import React from 'react';

import ProfileConainer from './components/Profile/ProfileConainer';
import HeaderContainer from './components/Header/HeaderContainer';
import Login from './components/Login/Login';
import { initializeApp } from './redux/appReducer';
import { Component } from 'react';
import { compose } from 'redux';
import { connect } from 'react-redux';
import Preloader from './components/Preloader/Preloader';
import { WithSuspense } from './hoc/withSuspense';


const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'));
const UsersConaitner = React.lazy(() => import('./components/Users/UsersContainer'));
// import DialogsContainer from './components/Dialogs/DialogsContainer';
// import UsersConaitner from './components/Users/UsersContainer';

class App extends Component {

  componentDidMount() {
    this.props.initializeApp();
  }

  render() {
    if(!this.props.initialized) {
      return <Preloader />
    } 

    return (
      <div>
        <div className={classes.app_wrapper}>
          <HeaderContainer />
          <Nav />
          <div className={classes.content}>
            <Route path='/profile/:userId?' render={() => <ProfileConainer />} />
            <Route path='/dialogs' render={() => WithSuspense(DialogsContainer)} />
            <Route path='/news' component={News} />
            <Route path='/music' component={Music} />
            <Route path='/settings' component={Settings} />
            <Route path='/users' render={() => WithSuspense(UsersConaitner) } />
            <Route path='/login' render={() => <Login />} />

          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  initialized: state.app.initialized
})

export default compose(
  withRouter,
  connect(mapStateToProps, { initializeApp }))(App);
