import axios from 'axios';

let instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
        "API-KEY": "fa53e217-7bfa-4afc-85c5-49c8e0bd20ee"
    }
})

export let getUsers = (currentPage, pageSize) => {
    return instance.get(`users?page=${currentPage}&count=${pageSize}`).then(response => {
        return response.data
    })
}

export let getAuth = () => {
    return instance.get(`auth/me`).then(response => {
        return response.data
    })
}

export let getProfile = (userId) => {
    return instance.get(`profile/${userId}`)
}

export let setUnfollow = (userId) => {
    return instance.delete(`follow/${userId}`)
}

export let setFollow = (userId) => {
    return instance.post(`follow/${userId}`)
}


export let getStatus = (userId) => {
    return instance.get(`profile/status/${userId}`)
}
export let updateStatus = (status) => {
    return instance.put(`profile/status`, {status: status} )
}

export let login = (email, password, rememberMe) => {
    return instance.post(`/auth/login`, {email, password, rememberMe})
}

export let logout = () => {
    return instance.delete(`/auth/login`)
}

